import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
export default {
	components: {
		uniLoadMore	
	},
	data() {
		return {
			cateMaskState: 0, //分类面板展开状态
			categoryId:-1,
			headerPosition:"fixed",
			headerTop:"0px",
			loadingType: 'more', //加载更多状态
			filterIndex: 0, 
			//cateId: 0, //已选三级分类id
			priceOrder: 0, //1 价格从低到高 2价格从高到低
			cateList: [],
			goodsList: [],
			pageNo:1,
			pageSize:10
		};
	},
	
	onLoad(options){



		//this.cateId = options.tid;
		this.categoryId = options.tid;
		this.loadCateList(options.fid,options.sid);
		this.loadData();
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh(){
		this.loadData('refresh');
	},
	//加载更多
	onReachBottom(){
		this.pageNo = this.pageNo + 1;
		this.loadData();
	},
	methods: {
		//加载分类
		async loadCateList(fid, sid){
			let _this = this;
			uni.request({
			    url: this.apiServer + "/api/category/list",
			    dataType: "JSON",
			    success: function(res) {
			      let list = res.data;
				  let cateList = list.filter(item=>item.level == fid); 
				  cateList.forEach(item=>{
				  	let tempList = list.filter(val=>val.level == item.id);
				  	item.child = tempList;
				  })
				  _this.cateList = cateList;
			    }
			});
			
			//let list = await this.$api.json('cateList');
			
		},
		//加载商品 ，带下拉刷新和上滑加载
		async loadData(type='add', loading) {
			//没有更多直接返回
			if(type === 'add'){
				if(this.loadingType === 'nomore'){
					return;
				}
				this.loadingType = 'loading';
			}else{
				this.loadingType = 'more'
			}
			//let goodsList = await this.$api.json('goodsList');
			let _this = this;
			let params = {
			          "categoryId":this.categoryId,
			          "pageNo":this.pageNo,
					  "pageSize":this.pageSize					  
			    }
			if(this.filterIndex === 1){	
				params.sortType = 1;
			}
		     if(_this.filterIndex === 2){
			    if(_this.priceOrder == 1){
				    params.sortType = 2;
				  }else{
				 	params.sortType = 3; 
			     }
	    	  }
			uni.request({
			    url: this.apiServer + "/api/category/listByCommodityId",
			    method: 'POST',
				dataType: "JSON",
				data:params,
			    success: function(res) {
			     let goodsList = res.data.list;
				  if(type === 'refresh'){
				  	_this.goodsList = [];
				  }
				  _this.goodsList = _this.goodsList.concat(goodsList);  
				  //判断是否还有下一页，有是more  没有是nomore(测试数据判断大于20就没有了)
				  _this.loadingType  = _this.goodsList.length >= res.data.total ? 'nomore' : 'more';
				  if(type === 'refresh'){
				  	if(loading == 1){
				  		uni.hideLoading()
				  	}else{
				  		uni.stopPullDownRefresh();
				  	}
				  }
			    }
			});
			
		},
		//筛选点击
		tabClick(index){
			if(this.filterIndex === index && index !== 2){
				return;
			}
			this.filterIndex = index;
			if(index === 2){
				this.priceOrder = this.priceOrder === 1 ? 2: 1;
			}else{
				this.priceOrder = 0;
			}
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.pageNo = 1;
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//显示分类面板
		toggleCateMask(type){
			let timer = type === 'show' ? 10 : 300;
			let	state = type === 'show' ? 1 : 0;
			this.cateMaskState = 2;
			setTimeout(()=>{
				this.cateMaskState = state;
			}, timer)
		},
		//分类点击
		changeCate(item){
			this.categoryId = item.id;
			this.toggleCateMask();
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.pageNo = 1;
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//详情
		navToDetailPage(item){
			//测试数据没有写id，用title代替
			let id = item.id;
			let userId = "";
			uni.getStorage({
			    key:"userInfo",
		     	success(e){
		      	userId = e.data.id//这就是你想要取的token
			}
			});
			uni.navigateTo({
				url: `/pages/product/product?id=${id}&uid=${userId}`
			})
		},
		stopPrevent(){}
	},
}
